.lMap{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: #f7f7f7;
    height: 92vh;
    color: #f45e59;
    gap: 10px;
}

.lMap>h1{
    padding-top: 15px;
}

.score--container{
    width: 40vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #62bef03b;
    padding: 20px;
    border-style:solid;
    border-width: 5px;
    border-color: rgb(75, 74, 74);
    margin-bottom: 10px;
    color: rgb(75, 74, 74);
}

.score--container:nth-child(2){
    background-color: rgba(0, 0, 0, 0.808);
    border-color: #f45e59;
    color: white;

}



.score--name{
    text-align: left;
    font-size: larger;
    font-weight: 900;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.score--time{
    font-size: larger;
    font-weight: 900;
    letter-spacing: 1px;
    text-transform: uppercase;
}