.navbar{
    height: 8vh;
    background-color: #f12e27c4;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.navbar > h1{
    font-size: 5vh;
}

.navbar--nav{
    display: flex;
    list-style-type: none;
    justify-content: space-around;
    gap: 12px;
}

.navbar--nav > li{
    cursor: pointer;
}