.options{
    position: absolute;
    background-color: white;
    max-width: 150px;
    border-radius: 10px;
    border-bottom: 1px grey;
    color: #002b55;
    cursor: pointer;

}

.options--char{
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.options--char > img{
    width: 30%;
    height: 30%;
}

.Square{
    position: absolute;
    content: url("../images/select_64.png");
    width: 64px;
    height: 64px;
}