.leaderboards{
    height: 92vh;
    background-color: #f7f7f7;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    color: #f45e59;
}

.leaderboards--map-col{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    background-color: #ffffff;
    color: #002b55;
    padding: 2vh;
    border-radius: 15px;
    border-style: solid;
    border-width: 2px;
    border-color: #f12e2783;
}

.leaderboards--title{
    margin: 2vh;
}

.leaderboards--grid{
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap-reverse;
    gap: 2vh;
}

.leaderboards--info{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.leaderboard--char--container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
}


.leaderboard-title{
    margin-left: 10px;
}

.leaderboard--img{
    height: 20vh;
    margin-bottom: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    border-bottom: solid rgba(128, 128, 128, 0.534) 1px;
}

.leaderboard--char{
    margin-right: 10px;
}

.leaderboard--char > img{
    width: 30px;
}