.Image{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #f7f7f7;
    color: #f45e59;
    height: 92vh;
}

.stopwatch{
    color: rgb(75, 74, 74);
}

.waldo{
    aspect-ratio: 16,9;
    max-height: 78vh;
    background-color: rgb(240, 79, 79);
    cursor: url("./../../images/select_64.png") 32 32, auto;
}

.findChar{
    display: flex;
    justify-content: center;
    align-items: center;
}

.findChar--container{
    display: flex;
    padding: 5px;
    justify-content: space-around;
    align-items: center;
    gap: 3vw;
    color:rgb(75, 74, 74);
    border: solid rgb(75, 74, 74) 2px;
    border-radius: 15px;

}

.findChar--container:empty{
    display: none;
}

.winMenu{
    display: flex;
    justify-content: space-around;
    gap: 3vw;
    margin-bottom: 10px;
}

.scoreForm{
    position: absolute;
    top: 50%; right: 50%;
    transform: translate(50%,-50%);
    width: 320px;
    height: 250px;
    color: rgb(240, 79, 79);
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    border-style: solid;
    border-width: 3px;
    border-color: rgb(240, 79, 79);
    gap: 5px;
    box-shadow: 0px 0px 10px 10px white;

}

.scoreForm > h2{
    font-size: 2em;
}

form{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
}

#name{
    font-size: 1.1em;
    font-weight: bolder;
}

input{
    text-align: center;
}

.quit{
    position: absolute;
    right: 0;
    top: 0;
    width: 20px;
    height: 20px;
    transform: translate(-10px,10px);
    background-color: rgb(255, 81, 0);
    border-style: none;
    border-radius: 10px;
    color: white;
    cursor:pointer;
}

form > button, .checkLead{
    margin: 5px;
    min-width: 40%;
    min-height: 4vh;
    font-family: 'Inter';
    font-size: 1.1em;
    background-color: #f45e59;
    border-style: none;
    border-radius: 15px;
    cursor: pointer;
    padding: 2px;
}
