.homescreen{
    height: 92vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #002b55;
}

.homescreen > img{
    aspect-ratio: 16,9;
}

.homescreen-text{
    position: absolute;
    top: 35%;
    left: 50%;
    width: 20vw;
    color: #e7e7e7;
    font-size: 5vh;
    text-align: center;
}

.homescreen-text--button{
    width: 20vw;
    height: 5vh;
    font-size: 3vh;
    font-family: 'Inter';
}

.homescreen-text--button:hover{
    width: 20vw;
    height: 5vh;
    font-size: 3vh;
    font-family: 'Inter';
    transform: translateY(5px);
    box-shadow: -2px -2px 2px 2px;
}